import { CompilerResult } from "../TiroCompiler";
import Editor from "./Editor";

import styles from "./CompilerView.module.scss";

export interface CompilerViewProps {
    compiling: boolean;
    initialSource?: string;
    result?: CompilerResult;
    onSourceChanged: (source: string) => void;
}

// Only client side import works.
const Split = ((): any => {
    if (typeof window === "undefined") return null;

    // eslint-disable-next-line @typescript-eslint/no-var-requires
    return require("react-split").default;
})();

function renderResult(result: CompilerResult): JSX.Element {
    const messages = result.messages.map((message, index) => {
        return <li key={index}>{message}</li>;
    });

    return (
        <div>
            <div>Success: {result.success.toString()}</div>
            <div>Time taken: {result.elapsedMillis.toFixed(3)} ms</div>
            <div>Messages:</div>
            <ul>{messages}</ul>
            <pre>{result.ast}</pre>
            <pre>{result.ir}</pre>
            <pre>{result.bytecode}</pre>
        </div>
    );
}

function ResultPane(props: { compiling: boolean; result?: CompilerResult }) {
    const { compiling, result } = props;

    const statusText = compiling ? <div>Comdiviling...</div> : null;
    const resultText = result ? renderResult(result) : null;
    return (
        <>
            {statusText}
            {resultText}
        </>
    );
}

export function CompilerView({
    compiling,
    initialSource,
    result,
    onSourceChanged,
}: CompilerViewProps): JSX.Element {
    return (
        <Split className={styles["split"]} sizes={[50, 50]} minSize={100}>
            <div className={styles["text-pane"]}>
                <Editor
                    initialSource={initialSource ?? ""}
                    onChange={onSourceChanged}
                ></Editor>
            </div>
            <div className={styles["result-pane"]}>
                {ResultPane(compiling, result)}
            </div>
        </Split>
    );
}
